import axios from "axios"
import qs from "qs"


// 请求拦截：组件发起请求,配置项先到了请求拦截,请求拦截可以修改请求配置,然后retur n的结果发送给后端
axios.interceptors.request.use(config => {
  console.log("========此处是请求拦截器=========");
  console.log(config);
  console.log("==========拦截器结束============");

  // 获取身份信息
  var status = sessionStorage.getItem("status")
  if (status) {
    config.status = status
  }
  return config
})



// 响应拦截：后端相应数据，先到响应拦截器，拦截器可以处理数据，return的数据就是给组件的数据
axios.interceptors.response.use((res) => {
  console.log("========此处是响应拦截器=========");
  console.log(res);
  console.log("==========拦截器结束============");
  if (!res.data.isok) {
    alert("请检查账号或密码")
  }
  return res
})

// 开发的时候
// const baseUrl = "/api"
// 上线之后
const baseUrl = ""

// 登录接口
export const login = (user) => {
  // user:{name:this.name,pass:this.pass}

  // 这种方式针对于收不到参数
  return axios({
    url: baseUrl+"/login",
    method: "post",
    data: qs.stringify(user)
  })

  // 这种方式针对于收不到文件
  // var form = new FormData()
  // for (let i in user) {
  //   form.append(i, user[i])
  // }
  // return axios({
  //   url: "/api/login",
  //   method: "post",
  //   data: form
  // })
}

// 外卖列表
export const food = () => {
  return axios({
    url: baseUrl+"/foodList",
    method: "get"
  })
}

// 外卖详情
export const foodDetail = id => {
  return axios({
    url: baseUrl+"/getFoodDetail",
    method: "get",
    params: {
      foodId: id
    }
  })
}

// 电影列表
export const movie = () => {
  return axios({
    url: baseUrl+"/movie",
    method: "get"
  })
}

// 电影详情
export const movieDetail = id => {
  return axios({
    url: baseUrl+"/getMovieDetail",
    method: "get",
    params: {
      id: id
    }
  })
}



